export default {
	data() {
		return {
			status: null, // 0:审核中;1:已通过;2:未通过;
			info: null,
			infoA: {
				status: 0,
				src: "http://qakj5dvcb.bkt.clouddn.com/static/status_checking.png",
				tip: "您的实名认证 正在审核中...",
				text: ["请关注手机短信，实名认证审核成功之后，您会收到认证成功的短信。"],
				btn: "我知道了"
			},
			infoB: {
				status: 1,
				src: "http://qakj5dvcb.bkt.clouddn.com/static/status_success.png",
				tip: "您的实名认证已通过",
				text: ["您提交的实名认证资料已通过审核，请尽快登录商家中心后台，完善您的店铺资料。"],
				btn: "登录商家中心"
			},
			infoC: {
				status: 2,
				src: "http://qakj5dvcb.bkt.clouddn.com/static/status_faild.png",
				tip: "您的实名认证未通过",
				text: ["请您重新修改资料之后再提交"],
				btn: "变更实名认证"
			}
		}
	},
	computed: {

	},
	onLoad(options) {
		console.log("options", options)
		const status = 1
		status == 0 ? this.info = this.infoA : status == 1 ? this.info = this.infoB : this.info = this.infoC;
	},
	methods: {
		navBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		switchBtn(status) {
			switch( status){
				case 1:
					uni.navigateTo({
						url: "/pages/login/login?login=shop"
					})
					break
				case 2:
					uni.navigateTo({
						url: "/pages/login/authentication?steps=3"
					})
					break
			}
		},
		
	}
}
